export default {
    FILTER_GALLERY_UNIT_DATA(state, galleryId) {
        state.galleryUnitData = state.galleryListData.filter(fiterByRouterPath)[0]

        function fiterByRouterPath(obj) {
            if (obj.routerPath == galleryId) {
                return true
            } else {
                return false
            }
        }
    },
    FILTER_BLOG_UNIT_DATA(state, blogId) {
        state.blogUnitData = state.blogListData.filter(fiterByRouterPath)[0]

        function fiterByRouterPath(obj) {
            if (obj.name == blogId) {
                return true
            } else {
                return false
            }
        }
    }
}



