<template>
    <div id="blog-view" class='wrapperStandard'>
        <div class="blog-full-content">
            <h3 class="blog-preview-unit-title">{{ blogUnitData.title }}</h3>
            <p class="blog-preview-unit-desc">{{ blogUnitData.date }}</p>
            <p v-for='parag in blogUnitData.content' class='blog-content-text'>
                {{ parag }}
            </p>
        </div>
    </div>
</template>

<script>
import { filterBlogUnitData } from '../../../vuex/actions'
import { getBlogUnitData } from '../../../vuex/getters'

export default {
    vuex: {
        getters: {
            blogUnitData: getBlogUnitData
        },
        actions: {
            filterBlogUnitData
        }
    },
    route: {
        data ({ to, next }) {
            this.filterBlogUnitData(to.params.blogName) //根据路由获取数据，放路由的数据模块里更合适
        }
    }
}

</script>

<style lang='stylus'>
/*去除按钮点击之后的蓝边*/
input,button,select,textarea
    outline none

#blog-view
    position relative
    /*width 100%*/
    margin-top 20px

.blog-full-content
    padding-left 7%
    padding-right 27%

.blog-preview-unit-desc
    text-align right
    padding-right 5px

@media screen and (max-width: 768px)
    .blog-full-content
        padding-left 5%
        padding-right 5%

</style>



