var paragraphs = []

var p_1 = '斯里兰卡，全称斯里兰卡民主社会主义共和国（The Democratic Socialist Republic of Sri Lanka） ，旧称锡兰，是个热带岛国，位于印度洋海上，南亚区域合作联盟的创始成员，联合国的一员，英联邦，G77，不结盟运动。古代中国曾经称其为已程不、狮子国、师子国、僧伽罗、楞伽岛。'
paragraphs.push(p_1)

var p_2 = '“斯里兰卡”在僧伽罗语中意为“乐土”或“光明富庶的土地”，有“宝石王国”的美称，被马可·波罗认为是最美丽的岛屿。因为它有美丽绝伦的海滨，神秘莫测的古城，丰富的自然遗产，以及独特迷人的历史与文化。'
paragraphs.push(p_2)

export default paragraphs



