<template>
    <div class="blog-preview-unit">
        <div class='blog-box'>
            <div class="blog-text-box" @click=routeTo()>
                <!-- <span class="blog-preview-unit-title">六郡良家最少年</span>
                <span class="blog-preview-unit-desc">戎装骏马照山川</span> -->
                <h3 class="blog-preview-unit-title">{{ blog.title }}</h3>
                <p class="blog-preview-unit-desc">{{ blog.date }}</p>
                <!-- <p v-for='parag in blog.content' class='blog-content-text'>
                    {{ parag }}
                </p> -->
                <p class='blog-content-text'>
                    {{ blogSummary }}
                </p>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['blog'],
    data() {
        return {
            blogSummary:'',
            blogName: this.blog.name
        }
    },
    methods: {
        routeTo:function() {
            router.go({ name: 'blog', params: { blogName: this.blogName }})
        }
    },
    ready: function() {
        this.blogSummary = this.blog.content[0].slice(0, 112) + ' ......' 
    }
}
</script>

<style lang='stylus'>
.blog-preview-unit
    padding-top 20px
    padding-bottom 20px
    /*padding-left 65px
    padding-right 255px*/
    padding-left 7%
    padding-right 27%
    text-align left

.blog-text-box
    padding-bottom 10px
    border-bottom #ececec dashed 1px

.blog-text-box:hover
    cursor pointer


.blog-content-text
    color #999
    font-size 13px
    line-height 2
    text-indent 2em

.blog-preview-image-box
    margin-left auto
    margin-right auto
    border-style solid
    border-width 1px
    border-color #ccc

.blog-preview-image-box:hover
    cursor pointer

.blog-preview-unit-title
    display block
    color #666
    font-size 16px

.blog-preview-unit-desc
    padding-top 5px
    display block
    color #ccc
    font-size 12px

</style>



