// 红磨坊绘图函数
function draw(canvas) {
    var context = canvas.getContext('2d')

    var width = canvas.width
    var height = canvas.height

    var xCenterBia = width / 100
    var yCenterBia = height / 50

    var radiusStep = height / 30

    context.fillStyle = 'black'
    context.fillRect(0, 0, width, height)

    var centerX = width / 2
    var centerY = height / 5 * 2
    var radius = height / 5
    var radius_2 = height / 5

    for (var angle = 0; angle < 160; angle += 0.4) {

        var currentCenterX = centerX + Math.sin(angle*5/180*Math.PI) * xCenterBia
        var currentCenterY = centerY + Math.cos(angle*7/180*Math.PI) * yCenterBia

        radius += 0.5
        var radiusHead = radius + Math.sin(angle*5/180*Math.PI) * radiusStep
        var xHeadPos = currentCenterX + radiusHead * Math.cos(angle/180*Math.PI)
        var yHeadPos = currentCenterY + radiusHead * Math.sin(angle/180*Math.PI)

        radius_2 -= 0.5
        var radiusTail = radius_2 + Math.cos(angle*7/180*Math.PI) * radiusStep
        var xTailPos = currentCenterX + radiusTail * Math.cos((angle + 180)/180*Math.PI)
        var yTailPos = currentCenterY + radiusTail * Math.sin((angle + 180)/180*Math.PI)

        context.beginPath()

        context.moveTo(xHeadPos, yHeadPos)
        context.lineTo(xTailPos, yTailPos)

        context.strokeStyle = 'rgba(209, 30, 96,' + (angle / 1080 + 0.05) + ')'
        context.lineWidth = 1

        context.stroke()
    }
}

export default draw



