import noise from '../noise'

function draw(canvas) {
    var width = canvas.width
    var height = canvas.height

    var context = canvas.getContext('2d')

    context.fillStyle = 'black'
    context.fillRect(0, 0, width, height)

    var centerX = width / 2
    var centerY = height / 5 * 2
    var radius = height / 6
    var radius_2 = height / 6
    var angleMax = 210

    noise.seed(Math.random())
    var xNoise = Math.random()
    var yNoise = Math.random()

    for (var angle = 0; angle < angleMax; angle += 0.7) {
        xNoise += 0.01
        yNoise += 0.02

        var pNoise = noise.simplex2(xNoise, yNoise)

        var currentCenterX = centerX + pNoise * 15
        var currentCenterY = centerY + pNoise * 20

        radius += 0.5
        var radiusHead = radius + pNoise * 30
        var xHeadPos = currentCenterX + radiusHead * Math.cos(angle/180*Math.PI)
        var yHeadPos = currentCenterY + radiusHead * Math.sin(angle/180*Math.PI)

        radius_2 -= 0.5
        var radiusTail = radius_2 + pNoise * 30
        var xTailPos = currentCenterX + radiusTail * Math.cos((angle + 180)/180*Math.PI)
        var yTailPos = currentCenterY + radiusTail * Math.sin((angle + 180)/180*Math.PI)

        context.beginPath()

        context.moveTo(xHeadPos, yHeadPos)
        context.lineTo(xTailPos, yTailPos)

        // context.strokeStyle = 'rgba(209, 30, 96,' + (angle / 1080 + 0.1) + ')'
        context.strokeStyle = 'rgba(200, 200, 200,' + ((angleMax - angle) / 1080 + 0.05) + ')'
        // context.strokeStyle = 'rgba(25, 175, 176,' + (angle / 1080 + 0.1) + ')'
        context.lineWidth = 1

        context.stroke()
    }
}

export default draw



