<template>
    <div class="gallery-preview-unit col1">
        <div class='gallery-box'>
            <div class='gallery-preview-box'>
                <!-- <img class="gallery-preview-image-box" src="../../../static/image/preview.png"> -->
                <img class="gallery-preview-image-box" :src="previewImage" @click='routeTo()'>
            </div>

            <div class="gallery-text-box">
                <!-- <span class="gallery-preview-unit-title">六郡良家最少年</span>
                <span class="gallery-preview-unit-desc">戎装骏马照山川</span> -->
                <span class="gallery-preview-unit-title">{{ gallery.name }}</span>
                <span class="gallery-preview-unit-desc">{{ gallery.desc }}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['gallery'],
    data() {
        return {
            gName: this.gallery.routerPath,
            // previewImage: this.gallery.routerPath
            previewImage: ''
        }
    },
    methods: {
        routeTo:function() {
            router.go({ name: 'gallery', params: { galleryName: this.gName }})
        }
    },
    ready: function() {
        this.previewImage = require('./asset/image/' + this.gName + '.png')
    }
}
</script>

<style lang='stylus'>
.gallery-preview-unit
    padding-top 30px
    padding-bottom 20px
    text-align center

.gallery-preview-image-box
    margin-left auto
    margin-right auto
    border-style solid
    border-width 1px
    border-color #ccc

.gallery-preview-image-box:hover
    cursor pointer

.gallery-preview-unit-title
    display block
    color #666
    font-size 16px

.gallery-preview-unit-desc
    padding-top 5px
    display block
    color #aaa
    font-size 12px

</style>



