export function configRouter (router) {

    router.map({
        '/projects': {
            component: require('./components/content/projects/ProjectPreviewWrapper.vue')
        },
        '/gallery': {
            component: require('./components/content/gallery/GalleryPreviewWrapper.vue')
        },
        '/gallery/:galleryName': {
            name: 'gallery',
            component: require('./components/content/gallery/GalleryView.vue')
        },
        '/blog': {
            component: require('./components/content/blog/BlogPreviewWrapper.vue')
        },
        '/blog/:blogName': {
            name: 'blog',
            component: require('./components/content/blog/BlogView.vue')
        },
        '/resume': {
            component: require('./components/content/resume/ResumeWrapper.vue')
        },
        '/': {
            component: require('./components/content/projects/ProjectPreviewWrapper.vue')
        },
        '*': {
            component: require('./components/content/projects/ProjectPreviewWrapper.vue')
        }
    })
}



