// 绘图生成png下载
function downLoadCanvasToPNG(canvasConfig, fileName, painter) {
    if (typeof(canvasConfig.width) != 'number' || typeof(canvasConfig.height) != 'number' || canvasConfig.width <= 0 || canvasConfig.height <= 0) {
        throw ('canvasConfig wrong')
    }

    var width = canvasConfig.width
    var height = canvasConfig.height

    var canvas = document.createElement('canvas')
    document.body.appendChild(canvas)

    canvas.width = width
    canvas.height = height

    if(typeof(painter) != 'function') {
        throw ('painter is not a function')
    }

    painter(canvas)

    var tridentVersion =  typeof ScriptEngineMajorVersion === "function" ?  ScriptEngineMajorVersion() : undefined

    if (tridentVersion) { //open new window if is ie
        var imagePath = canvas.toDataURL().replace("image/png", "image/octet-stream")
        var newWindow=window.open('about:blank','image from canvas')
        newWindow.document.write("<img src='" + imagePath + "' alt='from canvas'/>")
    } else { //download png if not ie
        exportCanvasAsPNG(canvas, fileName)
    }

    document.body.removeChild(canvas)

    function exportCanvasAsPNG(canvas, fileName) {
        var MIME_TYPE = "image/png";

        var imgURL = canvas.toDataURL(MIME_TYPE);

        var dlLink = document.createElement('a');
        dlLink.download = fileName;
        dlLink.href = imgURL;
        dlLink.dataset.downloadurl = [MIME_TYPE, dlLink.download, dlLink.href].join(':');

        document.body.appendChild(dlLink);
        dlLink.click();
        document.body.removeChild(dlLink);
    }
}

export default downLoadCanvasToPNG



