var paragraphs = []

var p_1 = '［一］词以境界为最上。有境界，则自成高格，自有名句。五代、北宋之词所以独绝者在此。'
paragraphs.push(p_1)

var p_2 = '［二］有造境，有写境，此“理想”与“写实”二派之所由分。然二者颇难分别，因大诗人所造之境必合乎自然，所写之境亦必邻于理想故也。'
paragraphs.push(p_2)

var p_3 = '［三］有有我之境，有无我之境。“泪眼问花花不语，乱红飞过秋千去①”，“可堪孤馆闭春寒，杜鹃声里斜阳暮②”，有我之境也。“采菊东篱下，悠然见南山③”，“寒波澹澹起，白鸟悠悠下④”，无我之境也。有我之境，以我观物，故物皆著我之色彩。无我之境，以物观物，故不知何者为我，何者为物。古人为词，写有我之境者为多。然未始不能写无我之境，此在豪杰之士能自树立耳。'
paragraphs.push(p_3)

export default paragraphs



