<template>
    <div class="project-preview-unit col1">
        <div class='box'>
            <div class='preview-box'>
                <img class="preview-image-box" v-bind:src='previewImage' @click='openLinkPath(linkPath)'>
            </div>

            <div class="text-box">
                <!-- <span class="preview-unit-title">摘得星辰满袖行</span>
                <span class="preview-unit-desc">从醉里 忆平生 可怜心事太峥嵘</span> -->
                <span class="preview-unit-title">{{ prj.name }}</span>
                <span class="preview-unit-desc">{{ prj.desc }}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: ['prj'],
    data() {
        return {
            previewImage: '',
            previewImagePath: this.prj.previewImage,
            linkPath: this.prj.linkPath
        }
    },
    methods: {
        openLinkPath:function(linkPath) {
            window.open(linkPath)
            // console.log(linkPath)
        }
    },
    ready: function() {
        // console.log(this.prj.previewImage)
        // console.log(this.previewImage)
        this.previewImage = require('./asset/image/' + this.previewImagePath)

    }
}
</script>

<style lang='stylus'>
.project-preview-unit
    padding-top 30px
    padding-bottom 20px
    text-align center

.preview-image-box
    margin-left auto
    margin-right auto
    border-style solid
    border-width 1px
    border-color #ccc
    width 100%
    max-width 200px

.preview-image-box:hover
    cursor pointer

.preview-unit-title
    display block
    color #666
    font-size 16px

.preview-unit-desc
    padding-top 5px
    display block
    color #aaa
    font-size 12px

</style>



