<template>
    <div id="footer" class='wrapperStandard'>
        <div id="ccImageWrapper">
            <img src="" id="ccImage" />
            <span id='copyrightDesc'>本网站所有内容适用《Creative Commons License》协议.</span>
        </div>

    </div>
</template>

<script>
var ccImagePath = require('../../static/image/cc.png')

export default {
    data() {
        return {

        }
    },
    ready: function() {
        var ccImage = document.getElementById('ccImage')
        ccImage.src = ccImagePath
    }
}
</script>

<style lang='stylus'>
#footer
    position relative
    margin-left auto
    margin-right auto
    margin-top 20px
    height 30px
    padding-bottom 20px
    border-top #B8BDC5 1px solid

#ccImageWrapper
    position absolute
    top 10px
    left 10px

#ccImage
    vertical-align middle

#copyrightDesc
    font-size 12px
    color #B8BDC5
    line-height 18px
    vertical-align middle
</style>



