<template>
    <div id="gallery-view" class='wrapperStandard'>
        <div id="wallpaper-preview">
            <img src="" id='wallpaperPreviewImage' class='img-gallery-preview'/>
        </div>
        <div id="gallery-view-sidebar">
            <!-- 标题和描述＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝ -->
            <div id="wallpaper-title">
                {{ galleryUnitData.name }}
            </div>
            <div id="wallpaper-desc">
                {{ galleryUnitData.desc }}
            </div>

            <div id="wallpaperDownloadControlWrapper">
                <!-- 下载尺寸选项＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝ -->
                <div id="wallpaper-select-wrapper">
                    <select v-model="selectedWallpaperSize" id='sizeSelector'>
                        <option :value='wallpaperSizeRadioOptions[0]' selected>{{wallpaperSizeRadioOptions[0]}}</option>
                        <option :value='wallpaperSizeRadioOptions[1]'>{{wallpaperSizeRadioOptions[1]}}</option>
                    </select>
                    <!-- {{selectedWallpaperSize}} -->
                </div>

                <div id="wallpaperSizeSet" v-if='showWallpaperSizeSet'>
                    高：<input type="text" v-model='customWallpaperSize.width' class='wallpaperSizeNum'> 宽：<input type="text" v-model='customWallpaperSize.height' class='wallpaperSizeNum'>
                </div>
                <!-- 下载按钮＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝＝ -->
                <div id="wallpaper-button-wrapper">
                    <!-- <div class="wallpaper-single-button-wrapper">
                        <input type="button" id="bt-wallpaper-regen" class='wallpaper-button' value="重绘">
                    </div> -->
                    <div class="wallpaper-single-button-wrapper">
                        <input type="button" id="bt-wallpaper-download" class='wallpaper-button' value="下载" @click='downloadWallpaper()'>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import createWallpaperPreview from '../../../static/createWallpaperPreview'
import downLoadCanvasToPNG from '../../../static/downLoadCanvasToPNG'

import { filterGalleryUnitData } from '../../../vuex/actions'
import { getGalleryUnitData } from '../../../vuex/getters'

export default {
    data() {
        return {
            galleryName: '',
            selectedWallpaperSize: '',
            wallpaperSizeRadioOptions: ['匹配当前显示器尺寸', '自定义高宽尺寸'],
            customWallpaperSize: {
                width: '',
                height: ''
            }
        }
    },
    vuex: {
        getters: {
            galleryUnitData: getGalleryUnitData
        },
        actions: {
            filterGalleryUnitData
        }
    },
    route: {
        data ({ to, next }) {
            // "this" is available
            // console.log(to.params.galleryName)
            this.galleryName = to.params.galleryName //路由传递的具名路由参数

        }
    },
    methods: {
        downloadWallpaper: ''
    },
    computed: {
        showWallpaperSizeSet: function() {
            if (this.selectedWallpaperSize == '自定义高宽尺寸') {
                return true
            } else {
                return false
            }
        }
    },
    ready: function() {
        // 执行筛选，获取需要的gallery信息
        this.filterGalleryUnitData(this.galleryName)
        // console.log(this.galleryUnitData)

        var wPainter = require('../../../static/gallery/' + this.galleryName)

        var canvasConfig = {
            width: 1200,
            height: 800
        }

        var container = document.getElementById('wallpaper-preview')

        createWallpaperPreview(canvasConfig, container, 'wallpaperPreviewImage', wPainter.default)
        //闭包传递下载函数到方法中，不在这里绑定按钮行为
        this.downloadWallpaper = function() {
            if (this.selectedWallpaperSize == '匹配当前显示器尺寸') {
                var wallpaperSizeConfig = {}
                wallpaperSizeConfig.width = +screen.width
                wallpaperSizeConfig.height = +screen.height

                var imageName = this.galleryName + '_' + wallpaperSizeConfig.width + '_' + wallpaperSizeConfig.height + '.png'

                downLoadCanvasToPNG(wallpaperSizeConfig, imageName, wPainter.default)
            } else {
                var wallpaperSizeConfig = {}
                wallpaperSizeConfig.width = +this.customWallpaperSize.width
                wallpaperSizeConfig.height = +this.customWallpaperSize.height

                if (wallpaperSizeConfig.width > 0 && wallpaperSizeConfig.height > 0) {
                    var imageName = this.galleryName + '_' + wallpaperSizeConfig.width + '_' + wallpaperSizeConfig.height + '.png'

                    downLoadCanvasToPNG(wallpaperSizeConfig, imageName, wPainter.default)
                } else {
                    alert('请输入大于0的数字')
                    return
                }
            }
        }
    }
}


</script>

<style lang='stylus'>
/*去除按钮点击之后的蓝边*/
input,button,select,textarea
    outline none

#gallery-view
    position relative
    /*width 100%*/
    margin-top 20px

#wallpaper-preview
    position relative
    width 78%
    margin-left 1%
    margin-right 5px
    background black

.img-gallery-preview
    max-width 100%

#gallery-view-sidebar
    position absolute
    left 79%
    top 0
    width 20%
    height 100%
    background #f9f9f9

#wallpaper-title
    position absolute
    top 40px
    left 50%
    transform translate(-50%, 0%)
    color #333
    font-size 16px

#wallpaper-desc
    position absolute
    top 60px
    left 50%
    transform translate(-50%, 0%)
    width 100%
    padding 5px
    text-align center
    color #aaa
    font-size 12px

#wallpaperDownloadControlWrapper
    position absolute
    bottom 30px
    left 50%
    transform translate(-50%, 0%)
    width 100%
    border-top #eee dashed 1px
    text-align center
    color #999
    font-size 12px

#wallpaper-select-wrapper
    position relative
    width 100%
    text-align center
    padding-top 20px
    padding-bottom 10px
    #sizeSelector
        opacity .7
        width 80%
        color #999 !important

#wallpaperSizeSet
    position relative
    width 100%
    color #999
    .wallpaperSizeNum
        width 20%
        font-size 10px

#wallpaper-button-wrapper
    position relative
    width 100%
    padding-top 20px

.wallpaper-single-button-wrapper
    display inline-block
    position relative
    width 100%
    text-align center

.wallpaper-button
    color #999
    font-size 10px
    /*background #2289f7*/
    background #fff
    width 80%
    height 25px
    border-radius 3px
    border #ccc solid 1px

.wallpaper-button:hover
    /*background #3c73f2*/
    background #fff
    color #666
    cursor pointer
    border #999 solid 1px

@media screen and (max-width: 768px)
    #wallpaper-preview
        position relative
        width 98%
        margin-left 1%
        margin-right 1%
    #gallery-view-sidebar
        position relative
        left 0
        width 98%
        height 20%
        margin-left 1%
        margin-right 1%
        height 100px
        background #f9f9f9
    #wallpaper-title
        position absolute
        top 10px
    #wallpaper-desc
        position absolute
        top 30px
    #wallpaper-select-wrapper
        display none
    #wallpaper-button-wrapper
        position relative
        top 65px
        left 0
        transform translate(0%, 0%)
        width 100%

</style>



